
import React from 'react';

import style from './Dialogs.module.scss'
import DialogItem from '../dialog/DialogItem';
import orderBy from 'lodash/orderBy'


function Dialogs({ userId }) {
    const messages =
        [
            {
                text: 'Я ща стрепсилс тебе куплю, п…',
                date: new Date('Thu May 07 2020 17:58:01'),
                isReaded: true,
                isMe: true,
                user: {
                    fullName: 'Jack the Ripper', online: true, _id: 2
                }
            },
            {
                text: 'Мы все свидетельствуем …',
                date: new Date('Thu May 08 2020 17:58:01'),
                isReaded: true,
                isMe: false,
                user: {
                    fullName: 'Фёдор Достоевский', online: true, _id: 1
                }
            },
            {
                text: 'Мы все свидетельствуем …',
                date: new Date('Thu May 08 2020 19:58:01'),
                isReaded: true,
                isMe: false,
                user: {
                    fullName: 'Фёдор Достоевский', online: true, _id: 1
                }
            },

        ]

    return (
        <div className={style.dialog__list}>

            {orderBy(messages, 'date', 'desc')
                .map(item => <DialogItem
                    message={item}
                    unReaded={10}
                    isMe={userId === item.user._id}
                />)}


        </div>
    )
}

export default Dialogs;
